.App {
  overflow: hidden;
  text-align: center;
  background-color: #343541;
}

.App-logo {
  height: 72px;
  pointer-events: none;
}

.HelpText {
  font-family: sans-serif;
  margin-top: 10px;
  font-size: 25px;
}

/*@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}*/

.TextBox {
  border-radius: 5px;
}

.Footer {
  display: flex;
  flex-direction: column;
  width: 100vw;
  margin-top: auto;
  justify-content: center;
  align-items: center;
  min-height: 15vh;
}

.disclaimer {
  font-family: sans-serif;
  font-size: 12px;
  color: #b0b0bd;
  margin-top: 1.5vh;
}

.App-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
  min-height: 70vh;
}

.TextBox {
  width: 92vw;
  height: 8vh;
  max-height: 50px;
  border-radius: 10px;
  border-style: solid;
  background-color: #343541;
  border-color: #555661;
  font-family: sans-serif;
  font-size: 100%;
  color: #f7f5ff;
  text-wrap: wrap;
  padding-left: 2vw;
  outline: none;
}

.Body {
  min-height: 15vh;
  display: flex;
  flex-wrap: wrap;
  margin-left: 2.8vw;
}

.Prompts {
  width: 47vw;
  height: 7.5vh;
}

textarea::placeholder {
    color: #f7f5ff;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
